'use client';

import { VisuallyHidden } from '@radix-ui/react-visually-hidden';
import { useRouter } from 'next/navigation';

import Typography from '@kapoo/global-ui-pack/atom/Typography';
import Img from '@kapoo/global-ui-pack/molecule/Img';
import MarkdownViewer from '@kapoo/global-ui-pack/organism/MarkdownViewer';
import Badge from '@kapoo/headless-ui-pack/components/new-york/Badge';
import type { DialogProps } from '@kapoo/headless-ui-pack/components/new-york/Dialog';
import Dialog from '@kapoo/headless-ui-pack/components/new-york/Dialog';
import Separator from '@kapoo/headless-ui-pack/components/new-york/Separator';

import ProjectCarousel from '../../molecule/ProjectCarousel';

import type { PropsWithChildren } from 'react';

export interface ProjectModalProps extends PropsWithChildren
{
	/**
	 * 제목
	 */
	readonly title: string;

	/**
	 * 부제목
	 */
	readonly subtitle: string[];

	/**
	 * 태그
	 */
	readonly tags: string[];

	/**
	 * 이미지 배열
	 */
	readonly images: string[];

	/**
	 * 아이콘
	 */
	readonly icon: string;
}

/**
 * 프로젝트 모달 organism 컴포넌트 반환 메서드
 *
 * @param {ProjectModalProps} param0: ProjectModalProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function ProjectModal({ title, subtitle, tags, images, icon, children }: ProjectModalProps): React.JSX.Element
{
	const router = useRouter();

	const handleClick: DialogProps['onOpenChange'] = (open) =>
	{
		if (!open)
		{
			router.replace('/projects', { scroll: false });
		}
	};

	return (
		<Dialog.Root defaultOpen onOpenChange={handleClick}>
			<Dialog.Content className="w-[calc(100%-40px)] max-w-4xl overflow-hidden p-0" data-component="ProjectModal">
				<VisuallyHidden>
					<Dialog.Title />
					<Dialog.Description />
				</VisuallyHidden>

				<div className="max-h-[calc(100vh-40px)] overflow-y-scroll">
					<div className="relative h-40 bg-amber-200">
						<div className="absolute -bottom-10 left-0 flex w-full items-center justify-center">
							<div className="aspect-square">
								<div className="overflow-hidden rounded-xl shadow">
									<Img alt="tes" height={96} src={images[0]} width={96} />
								</div>

								<div className="relative">
									<div className="bg-background absolute -right-4 -bottom-4 z-10 aspect-square overflow-hidden rounded-xl shadow">
										<Img alt="tes" height={36} src={icon} width={36} />
									</div>
								</div>
							</div>
						</div>
					</div>

					<div className="mt-16 flex flex-col p-4">
						<div className="flex flex-col items-center justify-center gap-2">
							<Typography variant="h3">{title}</Typography>

							<div className="flex flex-col items-center justify-center gap-1">
								{subtitle.map((i) => <Typography key={i} variant="muted">{i}</Typography>)}
							</div>
						</div>

						<div className="mt-6 flex flex-wrap items-center justify-center gap-2">
							{tags.map((tag) => <Badge key={tag}># {tag}</Badge>)}
						</div>

						<Separator className="my-10" />

						<div className="relative bg-gray-200 dark:bg-gray-900">
							<ProjectCarousel images={images} />
						</div>

						<MarkdownViewer color="blue">
							{/* eslint-disable-next-line @typescript-eslint/no-base-to-string */}
							{String(children)}
						</MarkdownViewer>
					</div>
				</div>
			</Dialog.Content>
		</Dialog.Root>
	);
}