'use client';

import { useEffect, useState } from 'react';

import { useMutationObserver } from '@kapoo/common';

import Typography from '../../atom/Typography';
import MarkdownCodeblock from '../MarkdownCodeblock';

import type { DetailedHTMLProps, HTMLAttributes } from 'react';

export type MarkdownCodeProps = DetailedHTMLProps<HTMLAttributes<HTMLElement>, HTMLElement>;

/**
 * 마크다운 코드 organism 컴포넌트 반환 메서드
 *
 * @param {MarkdownCodeProps} param0: MarkdownCodeProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function MarkdownCode({ children, className = '', ...props }: MarkdownCodeProps): React.JSX.Element
{
	const [
		themeState,
		setThemeState
	] = useState<ThemeType>();

	const regex = /^(language-)(.*?)$/.exec(className);
	const language = regex?.[2];

	useMutationObserver('body', () =>
	{
		const theme = document.body.classList.contains('dark');

		setThemeState(theme ? 'dark' : 'light');
	}, {
		attributeFilter: [ 'class' ],
		attributes: true
	});

	useEffect(() =>
	{
		const theme = localStorage.getItem('theme');

		if (theme !== null)
		{
			setThemeState(theme === 'dark' ? 'dark' : 'light');
		}
	}, []);

	// 정규식이 일치할 경우
	if (language === undefined)
	{
		return <Typography className={className} data-component="MarkdownCode" variant="inline-code">{children}</Typography>;
	}

	return (
		<MarkdownCodeblock className={className} data-component="MarkdownCode" languageName={language} theme={themeState} {...props}>
			{children}
		</MarkdownCodeblock>
	);
}