/**
 * 마크다운 a 태그 atom 컴포넌트
 *
 * @author RWB
 * @since 2025.03.20 Thu 12:48:16
 */

import { LinkIcon } from 'lucide-react';
import Link from 'next/link';

import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import Typography from '../Typography';

import type { AnchorHTMLAttributes, DetailedHTMLProps } from 'react';

export interface MarkdownAProps extends DetailedHTMLProps<AnchorHTMLAttributes<HTMLAnchorElement>, HTMLAnchorElement>
{
	/**
	 * 링크
	 */
	readonly href: string;

	/**
	 * 색상
	 */
	readonly color: 'blue' | 'green';
}

/**
 * 마크다운 a 태그 atom 컴포넌트 반환 메서드
 *
 * @param {MarkdownAProps} param0: MarkdownAProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function MarkdownA({ href, color, className, children, ...props }: MarkdownAProps): React.JSX.Element
{
	return (
		<Link
			className={cn({
				'border-blue-600 text-blue-600 dark:border-blue-400 dark:text-blue-400': color === 'blue',
				'border-green-600 text-green-600 dark:border-green-400 dark:text-green-400': color === 'green'
			},
			'center mx-1 inline-flex items-center gap-1 rounded border px-2 py-1 duration-300 hover:opacity-65', className)}
			data-component="MarkdownA"
			href={href}
			{...props}
		>
			<LinkIcon size={14} />
			<Typography variant="small">{children}</Typography>
		</Link>
	);
}