/**
 * 마크다운 툴팁 a 태그 molecule 컴포넌트
 *
 * @author RWB
 * @since 2025.03.21 Fri 00:28:26
 */

import Tooltip from '@kapoo/headless-ui-pack/components/new-york/Tooltip';
import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import MarkdownA from '../../atom/MarkdownA';

import type { MarkdownAProps } from '../../atom/MarkdownA';

export type MarkdownTooltipAProps = MarkdownAProps;

/**
 * 마크다운 툴팁 a 태그 molecule 컴포넌트 반환 메서드
 *
 * @param {MarkdownTooltipAProps} param0: MarkdownTooltipAProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function MarkdownTooltipA({ ...props }: MarkdownTooltipAProps): React.JSX.Element
{
	return (
		<Tooltip.Provider delayDuration={0.3}>
			<Tooltip.Root>
				<Tooltip.Trigger data-component="MarkdownTooltipA">
					<MarkdownA {...props} />
				</Tooltip.Trigger>

				<Tooltip.Content
					className={cn({
						'bg-blue-600 dark:bg-blue-400': props.color === 'blue',
						'bg-green-600 dark:bg-green-400': props.color === 'green'
					})}
				>
					{props.href}
				</Tooltip.Content>
			</Tooltip.Root>
		</Tooltip.Provider>
	);
}