import Typography from '../../atom/Typography';

import type { DetailedHTMLProps, ImgHTMLAttributes, MouseEventHandler } from 'react';

export interface MarkdownImgProps extends DetailedHTMLProps<ImgHTMLAttributes<HTMLImageElement>, HTMLImageElement>
{
	/**
	 * 이미지 클릭 메서드
	 */
	readonly onImageClick?: MouseEventHandler<HTMLButtonElement>;
}

/**
 * 마크다운 이미지 molecule 컴포넌트 반환 메서드
 *
 * @param {MarkdownImgProps} param0: MarkdownImgProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function MarkdownImg({ onImageClick, ...props }: MarkdownImgProps): React.JSX.Element
{
	return (
		<div className="flex flex-col items-center justify-center gap-4" data-component="MarkdownImg">
			<button className="overflow-hidden rounded shadow-lg" type="button" onClick={onImageClick}>
				{/* eslint-disable-next-line @next/next/no-img-element */}
				<img {...props} className="pointer-events-none h-auto w-full" />
			</button>

			{props.alt !== undefined && props.alt !== 'null' && <Typography variant="muted">{props.alt}</Typography>}
		</div>
	);
}