'use client';

import { useState } from 'react';
import ReactMarkdown from 'react-markdown';
import rehypeKatex from 'rehype-katex';
import rehypeRaw from 'rehype-raw';
import remarkGfm from 'remark-gfm';
import remarkMath from 'remark-math';

import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import MarkdownHeading from '../../atom/MarkdownHeading';
import MarkdownTable from '../../atom/MarkdownTable';
import Typography from '../../atom/Typography';
import MarkdownImg from '../../molecule/MarkdownImg';
import MarkdownTooltipA from '../../molecule/MarkdownTooltipA';
import ImageModal from '../ImageModal';
import MarkdownCode from '../MarkdownCode';

import type { ImageModalBaseHandler } from '../ImageModal';
import type { Options } from 'react-markdown';

export interface MarkdownViewerProps extends Options
{
	/**
	 * 색상
	 */
	readonly color: 'blue' | 'green';
}

/**
 * 마크다운 뷰어 organism 컴포넌트 반환 메서드
 *
 * @param {MarkdownViewerProps} param0: MarkdownViewerProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function MarkdownViewer({ color, ...props }: MarkdownViewerProps): React.JSX.Element
{
	const [
		imageState,
		setImageState
	] = useState<string>();

	const handleClose: ImageModalBaseHandler = () =>
	{
		setImageState(undefined);
	};

	return (
		<div data-component="MarkdownViewer">
			<ReactMarkdown
				components={{
					a: (prop) => <MarkdownTooltipA {...prop} color={color} href={prop.href!} target="_blank" />,
					blockquote: (prop) => <Typography variant="blockquote" {...prop} />,
					code: (prop) => <MarkdownCode {...prop} />,
					h1: (prop) => <MarkdownHeading {...prop} level={1} />,
					h2: (prop) => <MarkdownHeading {...prop} level={2} />,
					h3: (prop) => <MarkdownHeading {...prop} level={3} />,
					h4: (prop) => <MarkdownHeading {...prop} level={4} />,
					h5: (prop) => <MarkdownHeading {...prop} level={5} />,
					h6: (prop) => <MarkdownHeading {...prop} level={6} />,
					img: (prop) => (
						<MarkdownImg
							{...prop}
							onImageClick={() =>
							{
								setImageState(prop.src);
							}}
						/>
					),
					li: ({ className, ...prop }) => <li className={cn('ml-6 list-disc', className)} {...prop} />,
					p: ({ className, ...prop }) => <Typography className={cn('my-4', className)} {...prop} />,
					table: MarkdownTable
				}}
				rehypePlugins={[
					[
						rehypeKatex,
						{ output: 'mathml' }
					],
					rehypeRaw
				]}
				remarkPlugins={[
					remarkGfm,
					remarkMath
				]}
				{...props}
			/>

			<ImageModal
				open={imageState !== undefined}
				src={imageState}
				onClose={handleClose}
			/>
		</div>
	);
}