import { ChevronLeft, ChevronRight } from 'lucide-react';
import { useEffect, useState } from 'react';

import Typography from '@kapoo/global-ui-pack/atom/Typography';
import Img from '@kapoo/global-ui-pack/molecule/Img';
import Button from '@kapoo/headless-ui-pack/components/new-york/Button';
import Carousel from '@kapoo/headless-ui-pack/components/new-york/Carousel';
import type { CarouselApi } from '@kapoo/headless-ui-pack/components/new-york/Carousel/Carousel';

import type { MouseEventHandler } from 'react';

export interface ProjectCarouselProps
{
	/**
	 * 이미지 목록
	 */
	readonly images: string[];
}

/**
 * 프로젝트 캐러셀 molecule 컴포넌트 반환 메서드
 *
 * @param {ProjectCarouselProps} param0: ProjectCarouselProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function ProjectCarousel({ images }: ProjectCarouselProps): React.JSX.Element
{
	const [
		carouselApiState,
		setCarouselApiState
	] = useState<CarouselApi>();
	const [
		idxState,
		setIdxState
	] = useState<number>(0);

	const handlePrevClick: MouseEventHandler<HTMLButtonElement> = () =>
	{
		carouselApiState!.scrollPrev();
	};

	const handleNextClick: MouseEventHandler<HTMLButtonElement> = () =>
	{
		carouselApiState!.scrollNext();
	};

	useEffect(() =>
	{
		if (carouselApiState === undefined)
		{
			return;
		}

		const update = (): void =>
		{
			setIdxState(carouselApiState.selectedScrollSnap());
		};

		carouselApiState.on('select', update);

		return (): void =>
		{
			carouselApiState.off('select', update);
		};
	}, [ carouselApiState ]);

	return (
		<Carousel.Root
			opts={{ loop: true }}
			setApi={setCarouselApiState}
		>
			<Carousel.Content className="aspect-[16/9]" data-component="ProjectCarousel">
				{images.map((image) => (
					<Carousel.Item key={image}>
						<Img alt={image} className="object-contain" src={image} fill />
					</Carousel.Item>
				))}
			</Carousel.Content>

			<Button
				className="absolute top-1/2 left-2 -translate-y-1/2 opacity-70"
				data-component="ProjectCarousel-leftButton"
				size="icon"
				variant="ghost"
				asChild
				onClick={handlePrevClick}
			>
				<ChevronLeft className="drop-shadow" />
			</Button>

			<Button
				className="absolute top-1/2 right-2 -translate-y-1/2 opacity-70"
				data-component="ProjectCarousel-rightButton"
				size="icon"
				variant="ghost"
				asChild
				onClick={handleNextClick}
			>
				<ChevronRight className="drop-shadow" />
			</Button>

			<div className="bg-background/80 absolute bottom-2 left-1/2 -translate-x-1/2 rounded px-2">
				<Typography variant="small">{idxState + 1} / {images.length}</Typography>
			</div>
		</Carousel.Root>
	);
}