/**
 * 이미지 모달 organism 컴포넌트
 *
 * @author RWB
 * @since 2025.02.24 Mon 10:26:23
 */

'use client';

import { Minus, Plus, RefreshCw, X } from 'lucide-react';
import { useCallback, useEffect, useLayoutEffect, useState } from 'react';

import Button from '@kapoo/headless-ui-pack/components/new-york/Button';
import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import Img from '../../molecule/Img';
import ZoomPanner from '../ZoomPanner';

import type { ZoomPannerControllerProps, ZoomPannerZoomChangeHandler } from '../ZoomPanner';
import type { DetailedHTMLProps, HTMLAttributes } from 'react';

export type ImageModalDimType = 'blur' | 'modal' | 'none';
export type ImageModalBaseHandler = () => void;

export interface ImageModalProps extends DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>
{
	/**
	 * 이미지 src
	 */
	readonly src?: string;

	/**
	 * 열림 여부
	 */
	readonly open?: boolean;

	/**
	 * 딤 타입
	 *
	 * @default blur
	 *
	 * - blur: 블러 처리
	 * - modal: 어두운 뒷배경 처리
	 * - none: 아무 동작 없음
	 */
	readonly dimType?: ImageModalDimType;

	/**
	 * 닫기 이벤트 메서드
	 */
	readonly onClose?: ImageModalBaseHandler;

	/**
	 * 줌 변경 이벤트 메서드
	 */
	readonly onZoomChange?: ZoomPannerZoomChangeHandler;

	/**
	 * 줌 인 이벤트 메서드
	 */
	readonly onZoomIn?: ImageModalBaseHandler;

	/**
	 * 줌 아웃 이벤트 메서드
	 */
	readonly onZoomOut?: ImageModalBaseHandler;

	/**
	 * 리셋 이벤트 메서드
	 */
	readonly onReset?: ImageModalBaseHandler;
}

/**
 * 이미지 모달 organism 컴포넌트 반환 메서드
 *
 * @param {ImageModalProps} param0: ImageModalProps
 *
 * @returns {React.JSX.Element | null} JSX
 */
export default function ImageModal({ src = '', open, dimType = 'blur', onClose, onZoomChange, onZoomIn, onZoomOut, onReset, ...props }: ImageModalProps): React.JSX.Element | null
{
	const [
		controllerState,
		setControllerState
	] = useState<ZoomPannerControllerProps>();

	const handleClose = useCallback(() =>
	{
		onClose?.();
	}, [ onClose ]);

	const handleZoomIn = useCallback(() =>
	{
		controllerState!.zoomIn();

		onZoomIn?.();
	}, [
		controllerState,
		onZoomIn
	]);

	const handleZoomOut = useCallback(() =>
	{
		controllerState!.zoomOut();

		onZoomOut?.();
	}, [
		controllerState,
		onZoomOut
	]);

	const handleReset = useCallback(() =>
	{
		controllerState!.reset();

		onReset?.();
	}, [
		controllerState,
		onReset
	]);

	useLayoutEffect(() =>
	{
		document.body.style.overflow = open === true ? 'hidden' : '';
	}, [ open ]);

	useEffect(() =>
	{
		const handle = (e: KeyboardEvent): void =>
		{
			// ESC를 누를 경우
			if (e.key === 'Escape')
			{
				onClose?.();
			}
		};

		window.addEventListener('keydown', handle);

		return (): void =>
		{
			window.removeEventListener('keydown', handle);
		};
	}, [ onClose ]);

	useLayoutEffect(() =>
	{
		// 모달이 열릴 경우
		if (open === true)
		{
			controllerState?.reset();
		}
	}, [
		open,
		controllerState
	]);

	return open === true ? (
		(
			<div
				className={cn('fixed top-0 left-0 z-50 size-full', {
					'bg-black/80': dimType === 'modal',
					'bg-black/80 backdrop-blur-sm': dimType === 'blur'
				})}
				data-component="ImageModal"
				{...props}
			>
				<div className="absolute top-4 right-4 z-10">
					<Button size="icon" variant="link" onClick={handleClose}>
						<X color="white" />
					</Button>
				</div>

				<ZoomPanner controller={setControllerState} onZoomChange={onZoomChange}>
					<div className="flex size-full items-center justify-center">
						<div className="relative size-4/5">
							<Img alt={src} className="object-contain drop-shadow-lg" src={src} fill />
						</div>
					</div>
				</ZoomPanner>

				<div className="dark absolute right-4 bottom-4 z-10 flex flex-col gap-2">
					<Button size="icon" variant="default" onClick={handleZoomIn}>
						<Plus />
					</Button>

					<Button size="icon" variant="default" onClick={handleZoomOut}>
						<Minus />
					</Button>

					<Button size="icon" variant="default" onClick={handleReset}>
						<RefreshCw />
					</Button>
				</div>
			</div>
		)
	) : null;
}