/**
 * 마크다운 헤딩 태그 atom 컴포넌트
 *
 * @author RWB
 * @since 2025.03.21 Fri 01:28:05
 */

import Link from 'next/link';

import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import type { DetailedHTMLProps, HTMLAttributes } from 'react';

export type MarkdownHeadingType = 1 | 2 | 3 | 4 | 5 | 6;

export interface MarkdownHeadingProps extends DetailedHTMLProps<HTMLAttributes<HTMLHeadingElement>, HTMLHeadingElement>
{
	/**
	 * 레벨
	 */
	readonly level: MarkdownHeadingType;
}

/**
 * 마크다운 헤딩 태그 atom 컴포넌트 반환 메서드
 *
 * @param {MarkdownHeadingProps} param0: MarkdownHeadingProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function MarkdownHeading({ level, className, ...props }: MarkdownHeadingProps): React.JSX.Element
{
	// eslint-disable-next-line @typescript-eslint/no-base-to-string
	const href = String(props.children)
		.replaceAll(' ', '-');

	return (
		<div className="pt-10 pb-4" data-component="MarkdownHeading">
			{level === 1 && (
				<>
					<Link className="inline-block w-fit scroll-mt-16" href={`#${href}`} id={href}>
						<h1 className={cn('text-4xl font-bold', className)} {...props} />
					</Link>

					<div className="mt-1 w-full border-b" />
				</>
			)}
			{level === 2 && (
				<>
					<Link className="inline-block w-fit scroll-mt-16" href={`#${href}`} id={href}>
						<h2 className={cn('text-3xl font-bold', className)} {...props} />
					</Link>

					<div className="mt-1 w-full border-b" />
				</>
			)}
			{level === 3 && (
				<>
					<Link className="inline-block w-fit scroll-mt-16" href={`#${href}`} id={href}>
						<h3 className={cn('text-2xl font-bold', className)} {...props} />
					</Link>

					<div className="mt-1 w-full border-b" />
				</>
			)}
			{level === 4 && (
				<Link className="inline-block w-fit scroll-mt-16" href={`#${href}`} id={href}>
					<h4 className={cn('text-xl font-bold', className)} {...props} />
				</Link>
			)}
			{level === 5 && (
				<Link className="inline-block w-fit scroll-mt-16" href={`#${href}`} id={href}>
					<h5 className={cn('text-lg font-bold', className)} {...props} />
				</Link>
			)}
			{level === 6 && (
				<Link className="inline-block w-fit scroll-mt-16" href={`#${href}`} id={href}>
					<h6 className={cn('font-bold', className)} {...props} />
				</Link>
			)}
		</div>
	);
}